.notif {
    position: relative;
    background-color: #323232;
    padding: 0.8em 1em;
    vertical-align: middle;
    color: #ddd;
    box-shadow: 0 7px 18px rgba(0,0,0,0.2);
    border-radius: 3px;
    margin: 0.6em 0;
    pointer-events: auto;
    max-width: 400px;
    line-height: 1.5em;
  }

  .notif strong{
      color: #fff;
  }
  
  .show {
    -webkit-animation: show 0.5s;
            animation: show 0.5s;
  }
  
  @-webkit-keyframes show {
    from { -webkit-transform: translateY(-30%); transform: translateY(-30%) }
    to { -webkit-transform: translateY(0); transform: translateY(0) }
  }
  
  @keyframes show {
    from { -webkit-transform: translateY(-30%); transform: translateY(-30%) }
    to { -webkit-transform: translateY(0); transform: translateY(0) }
  }
.ot-login-page{
